import { Avatar, Comment, Tooltip } from "antd";
import moment from "moment";
import React from "react";
import styled from "styled-components";

const StyledComment = styled(Comment)`
  .ant-comment-inner {
    padding-bottom: 0px;
  }
  .ant-comment-avatar {
    cursor: initial;
  }
`;

export const CommentList = ({ comments, user }: any) => {
  return (
    <div>
      {comments.map((comment, index) => (
        <StyledComment
          // actions={actions}
          key={index}
          author={user ? user.displayName : "Someone"}
          avatar={
            <Avatar
              size="small"
              icon={!user || comment.uid !== user.uid ? "user" : undefined}
              src={user && comment.uid === user.uid ? user.photoURL : undefined}
            />
          }
          content={<p>{comment.content}</p>}
          datetime={
            <Tooltip
              title={moment(comment.createdAt.toDate()).format(
                "YYYY-MM-DD HH:mm:ss"
              )}
            >
              <span>{moment(comment.createdAt.toDate()).fromNow()}</span>
            </Tooltip>
          }
        />
      ))}
    </div>
  );
};
